<template>
    <div class="faq">
        <h1>
            FAQ
        </h1>

        <div class="faqBox">
            <div class="faqTitle" @click="[contentShow.contentShow1 = !contentShow.contentShow1, arrowRotate($event)]">
                <h2>
                    Q. 배송이 가능한가요?
                </h2>
                <i class="fa-solid fa-caret-down"></i>
            </div>
            <div class="faqContent" v-if="contentShow.contentShow1">
                <p>
                    노티드에서는 배송 서비스를 지원하지 않습니다.<br>
                    사이트에서 구매하신 제품은 원하는 지점/시간을 선택하신 후 해당 지점에 방문해 직접 수령하셔야 합니다.
                </p>
            </div>
        </div>
        <div class="faqBox">
            <div class="faqTitle" @click="[contentShow.contentShow2 = !contentShow.contentShow2, arrowRotate($event)]">
                <h2>
                    Q. 상품 예약 구매 후 픽업 날짜/장소를 변경할 수 있나요?
                </h2>
                <i class="fa-solid fa-caret-down"></i>
            </div>
            <div class="faqContent" v-if="contentShow.contentShow2">
                <p>
                    픽업 가능 시간이 완료되기 1시간 이내까지 가능합니다.<br>
                    픽업 날짜/장소 변경은 &#91;마이페이지&#93; - &#91;주문내역 확인&#93;란에서 변경 가능합니다.
                </p>
            </div>
        </div>
        <div class="faqBox">
            <div class="faqTitle" @click="[contentShow.contentShow3 = !contentShow.contentShow3, arrowRotate($event)]">
                <h2>
                    Q. 픽업 가능 시간 내 미방문/미수령 시에는 어떻게 되나요?
                </h2>
                <i class="fa-solid fa-caret-down"></i>
            </div>
            <div class="faqContent" v-if="contentShow.contentShow3">
                <p>
                    미방문/미수령 시에는 자동환불 처리됩니다.<br>
                    이에 대한 사전 알림은 별도로 안내되지 않기에 픽업 가능 기간 숙지하여 이용에 불편없으시길 부탁드립니다.
                </p>
            </div>
        </div>
        <div class="faqBox">
            <div class="faqTitle" @click="[contentShow.contentShow4 = !contentShow.contentShow4, arrowRotate($event)]">
                <h2>
                    Q. 픽업 상품 반품 시에 유의해야 하는 사항은 있나요?
                </h2>
                <i class="fa-solid fa-caret-down"></i>
            </div>
            <div class="faqContent" v-if="contentShow.contentShow4">
                <ul>
                    <li>
                        반품 시, 픽업하신 매장에 다시 방문하여 반품요청 부탁드립니다.
                    </li>
                    <li>
                        매장에서 반품 요청 시, 구매 영수증이나 &#91;마이페이지&#93; - &#91;주문내역 확인&#93;란에서 픽업 시간을 보여주시길 바랍니다.
                    </li>
                    <li>
                        반품은 제품 특성 상 픽업 후 1시간 이내에만 가능하다는 점 주의해주시길 바랍니다.
                    </li>
                </ul>

            </div>
        </div>
    </div>
</template>

<script>

    export default{
        name: 'FaqComponent',
        data: function () {
            return{
                contentShow: [
                    {contentShow1: false},
                    {contentShow2: false},
                    {contentShow3: false},
                    {contentShow4: false}
                ]
            }
        },
        methods: {
            arrowRotate: function (thisArrow) {
                var target = thisArrow.target;
                var tParent = target.parentNode;
                var tChild = tParent.childNodes[1];

                tChild.classList.toggle("arrowRotate");
            }
        }
    }

</script>

<style lang="scss">
    
    @import "../assets/scss/common.scss";

    .arrowRotate{
        transform: rotate(180deg);
    }

    .faq{
        width: 100%; height: auto;

        h1{
            width: 75%;
            margin: 0 auto 2vw;
            padding: 0 1vw;
            font-family: $titleFont;
            font-size: 5vw;
            line-height: 5.2vw;
        }

        .faqBox{
            width: 75%; height: auto;
            margin: 0 auto;
        }

        .faqTitle{
            width: 100%; height: 4vw;
            border-bottom: 0.15vw solid $fontColor;
            padding: 0 2vw;
            display: flex;
            justify-content: space-between;
            align-items: center;
            cursor: pointer;

            h2{
                font-family: $mainFont;
                font-size: 1.5vw;
                font-weight: 700;
            }

            i{
                display: inline-block;
                font-size: 2.5vw;
                padding: 0.5vw 0;
            }
        }


        .faqContent{
            width: 100%; height: auto;
            padding: 1vw 2vw;
            background-color: #fff;
            margin: 1vw 0;
            border-radius: 1.5vw;
                
            p, li{
                font-family: $mainFont;
                font-size: 1.2vw;
                font-weight: 700;
                line-height: 2vw;
            }

            ul{
                width: 100%;

                li{
                    list-style: disc;
                    list-style-position: inside;
                }
            }
        }
    }
</style>