<template>
    <div class="product">
        <div class="productImg">
            <img v-bind:src=product_src alt="product">
        </div>
        <h3>{{ product_name }}</h3>
        <span>{{ product_type }}</span>
        <div class="productInfo">
            <span class="price">{{ product_price }}</span>
            <i class="fa-solid fa-heart" @click="[clickIcon($event), addItem($event)]"></i>
            <i class="fa-solid fa-cart-shopping" @click="[clickIcon($event), addItem($event)]"></i>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ProductComponent',
    data: function () {
        return {
            itemBox: ""
        }
    },
    props: {
        product_src: String,
        product_name: String,
        product_type: String,
        product_price: String
    },
    methods: {
        clickIcon: function (thisIcon) {
            var target = thisIcon.target;
            target.classList.toggle("addColor");
        },
        addItem: function(thisItem){
          var target = thisItem.target;
          var itemPopup = document.querySelector(".itemPopup");

          if(target.classList.contains("addColor")){
            itemPopup.style.opacity = 1;

            setTimeout(function () {
              itemPopup.style.opacity = 0;
            }, 1500)
          }
        }
    }
}
</script>

<style scoped lang="scss">
@import "../assets/scss/common.scss";

.addColor {
    color: $pink !important;
}

.product {
    width: 19vw;
    height: 26vw;
    border: 0.15vw solid $fontColor;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 2.01vw 2vw 0;


    img {
        width: 100%;
        margin: 0.6vw 0;
    }

    h3 {
        width: 85%;
        border-top: 0.13vw solid $fontColor;
        font-family: $titleFont;
        font-size: 1.5vw;
        padding-top: 0.6vw;
    }

    span {
        font-family: $mainFont;
        font-weight: 700;
        font-size: 1.1vw;
        display: inline-block;
        width: 100%;
        padding-left: 1.5vw;
        text-transform: uppercase;
    }

    i {
        font-size: 1.5vw;
        -webkit-text-stroke: 0.13vw $fontColor;
        color: transparent;
        cursor: pointer;

        &:hover {
            color: $pink;
        }
    }

    .productInfo {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 0 1.5vw;
    }

    .price {
        width: 70%;
        text-align: left;
        padding: 0;
    }
}
</style>
